.payment-container{
    padding: 40px;
    padding-bottom: 0px;
}

.paymentBackBtn{
    width: 120px !important;
    border: 1px solid #1b1918 !important;
    border-radius: 12px !important;
    font-size: 18px !important;
    font-style: normal !important;
    font-weight: 600 !important;
    margin-bottom: 32px !important;
}

.payment-container-title{
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    margin: 0px 0px 40px 0px;
    text-align: left;
}

.tab-grid-1{
    .tab
    {
        width: 100px !important;
        font-size: 15px !important;
        white-space: nowrap !important;
    }
}


.payment-delivery-address h4{
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    margin: 0 0 16px 0;
}

.payment-delivery-address span{
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    margin: 0px;    
}

.payment-delivery-address{
    border-bottom: 1px solid #dcdcdc;
    padding-bottom: 24px;
}

.payment-price-details-container{
    border-bottom: 1px solid #dcdcdc;
    padding-bottom: 24px;
}

.payment-price-details-container h4{
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    margin: 24px 0 16px 0;
}

.payment-price-details{
    display: flex;
    flex-direction: column;
}

.payment-total-mrp,.payment-wallrus-coins{
    display: flex;
    justify-content: space-between;
}

.payment-total-mrp{
    margin-bottom: 16px;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
}

.payment-wallrus-coins{
    font-size: 16px;
    font-style: normal;
    font-weight: 500;

    span:last-child{
        color: #00B879;
    }
}

.payment-total-amount-container{
    margin-top: 20px;
    margin-bottom: 48px;
}

.payment-total-amount{
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    margin-bottom: 24px;
}

.credit-debit-card-container{
    padding-right: 30%;
    margin: 24px 0px 60px 0px;
}

.payment-page-right-grid{
    padding-left: 24px !important;
}

#card-number{
    letter-spacing: 5px !important;
}

#name-on-card{
    text-transform: uppercase;
}

.card-inputs{
    margin-bottom: 48px !important;
}

.cvv-container{
    padding-left: 24px;
}

.validity-container{
    padding-right: 24px;
}



#saved-option-cvv-input[type=number]::-webkit-inner-spin-button, 
#saved-option-cvv-input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
}

#saved-option-cvv-input[type=number] {
  -moz-appearance: textfield;
}



#cvv[type=number]::-webkit-inner-spin-button, 
#cvv[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
}

#cvv[type=number] {
  -moz-appearance: textfield;
}

#card-number[type=number]::-webkit-inner-spin-button, 
#card-number[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
}

#card-number[type=number] {
  -moz-appearance: textfield;
}


.PayNowBtn{
    background-color: #000 !important;
    color: #fff !important;
    border-radius: 8px !important;
    margin-top: 28px !important;
    margin-bottom: 28px !important;

    &:hover{
        background-color: #1b1918 !important;
    }
}

.upi-radio-labels{
    display: flex;
    align-items: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
}

.phonepe-icon{
    margin-right: 10px;
    height: 48px;
    width: 48px;
}

.googlepay-icon{
    margin-right: 10px;
    height: 48px;
    width: 48px;
}

.upiid-icon{
    margin-right: 10px;
    height: 48px;
    width: 48px;
}

.upi-container{
    margin-top: 30px;
    margin-bottom: 48px;
    padding-right: 30%;
}

.saved-option-container{
    margin-top: 30px;
    margin-bottom: 48px;
    padding-right: 30%;
}

.saved-option-labels{
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
}

.saved-card-details{
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    color: #6F6F6F;
}

.upi-id-input{
    margin-bottom: -5px !important;
}

.upi-id-input-container{
    padding-left: 30px !important;
}

.paymentBackArrow{
    display: none;
}


@media (max-width:960px) {


    .payment-page-right-grid{
    padding-left: 0 !important;
    }

    .tab-grid-1{
        border: none !important;
    }

    .tab-grid-2{
        border: none !important;
    }

    .cvv-container{
    padding-left: 14px;
    }

    .validity-container{
    padding-right: 5px;
    }

    .payment-container{
    padding: 20px;
    }

    .payment-container-title{
    font-size: 18px;
    text-align: left;
    margin-bottom: 10px;
    margin-top: 15px;
    }

    .paymentBackBtn{
        display: none !important;
    }

    
    .paymentBackArrow{
        display: block;
        height:24px;
        width:24px;
        z-index: 5;
        position: absolute;
    }

    .paymentBackArrowContainer{
        position: relative;
        height: 30px;
        width: 30px;
        z-index: 5;
    }

    .credit-debit-card-container{
    padding-right: 0;
    margin: 24px 0px 60px 0px;
    }
}

@media (max-width:600px){
    
    .tab-grid-1{
        .tab
        {
            width: 135px !important;
            font-size: 15px !important;
            white-space: nowrap !important;
        }
    }
}