.tab-shop {
  width: 100px !important;
  font-size: 16px !important;
  white-space: nowrap !important;
  margin-right: 20px !important;
  padding: 0 !important;
}

.tab-dotmenu {
  margin-left: 10px;
}

.tab-dotmenu-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
