@import "../../AppVariables.scss";

.aboutYou {
  display: flex;
  flex-direction: column;
}

.about-you-title {
  font-size: 28px;
  color: $primary-black;
  margin-bottom: 0;
}

.text-muted-tags {
  margin-top: -30px !important;
  margin-bottom: 30px !important;
  display: flex;
  align-items: center;
}

.about-you-sub-title {
  font-size: 18px;
  line-height: 24px;
  margin-top: 5px;
  color: $secondary-black;
  margin-bottom: 50px;
}

.profilePicDiv {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.uploadedPic {
  display: none;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: relative;
}
.editProfilePic {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: relative;
}

.profilePic {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px dashed $profile-border;
  border-radius: 50%;
  height: 130px;
  width: 130px;
  margin-right: 30px;
}

.loadedProfilePic {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  height: 130px;
  width: 130px;
  margin-right: 30px;
}

.about-you-input {
  margin: 20px 0 !important;
}

.about-you-input-address {
  margin: 35px 0 !important;
}

.profilePicUpload {
  visibility: hidden;
}

.profilePicUpload::before {
  content: "Upload profile pic";
  display: inline-block;
  background: #ffffff;
  border: 1px solid #000000;
  border-radius: 8px;
  padding: 8px 15px;
  white-space: nowrap;
  cursor: pointer;
  font-weight: 700;
  font-size: 14px;
  text-align: center;
  margin-right: 20px;
  visibility: visible;
}

.profile-pic-warning {
  color: #f44336;
  opacity: 0.8;
  font-size: 12px;
  font-weight: 600;
  position: relative;
  top: -1.5%;
  left: 1.5%;
}

.profilePicUpload:hover::before {
  background-color: $button-gray;
}

@media (max-width: 960px) {
  .i-img {
    margin-left: 60px;
    height: 12px !important;
    width: 12px !important;
  }
  .text-muted-tags {
    margin-top: -30px !important;
    margin-bottom: 30px !important;
    display: flex;
    align-items: center;
    margin-left: 10px !important;
  }
  .about-you-title {
    font-size: 18px;
    color: $primary-black;
    margin-bottom: 0;
  }

  .about-you-sub-title {
    font-size: 14px;
    margin-top: 5px;
    color: $secondary-black;
    margin-bottom: 50px;
  }

  .cameraIcon {
    height: 24px !important;
    width: 24px !important;
    margin-left: 113px;
  }
}

@media (min-width: 600px) and (max-width: 960px) {
  .cameraIcon {
    margin-left: 0px !important;
  }
  .profilePicUpload::before {
    margin-left: 0px;
  }
}

@media (max-width: 450px) {
  .profilePicDiv {
    display: flex;
    justify-content: space-between !important;
    width: 100% !important;
  }
  .profilePic {
    height: 120px;
    width: 120px;
    margin-right: -100px;
  }
  .profilePicUpload::before {
    padding: 8px 15px;
    margin-right: 40px;
    margin-left: 30px;
    font-size: 12px;
  }
}

@media (max-width: 375px) {
  .profilePicUpload::before {
    padding: 8px 15px;
    margin-right: 40px;
    margin-left: 90px;
    font-size: 12px;
  }
}
