.select-app {
  button {
    font-style: normal !important;
    font-weight: 500 !important;
    font-size: 14px !important;
    // color: #1B1918 !important;
    margin: 10px 10px 0px 0px !important;
    border-radius: 24px !important;
    border: 1px solid #dcdcdc !important;
    padding: 8px 20px !important;
    cursor: pointer !important;

    // &:focus-within
    // {
    //    background-color: #000 !important;
    //    color: #fff !important;
    // }
  }

  .selected-app {
    background-color: #1b1918 !important;
    color: #fff !important;
  }
}

.upload-design-final {
  background: #1b1918 !important;
  border-radius: 12px !important;
  font-style: normal !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  color: #ffffff !important;
  margin-top: 40px !important;
  height: 60px !important;
}

.upload-design-final-disabled {
  background: #6f6f6f !important;
  border-radius: 12px !important;
  font-style: normal !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  color: #ffffff !important;
  margin-top: 40px !important;
  height: 60px !important;
}

.add-new-colorway {
  border: 1px solid #1b1918 !important;
  border-radius: 8px !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 16px !important;
  color: #1b1918 !important;
  text-align: left !important;
  margin: 40px 0px 20px 0 !important;
  height: 60px !important;

  &:hover {
    background-color: #fff !important;
  }

  img {
    margin-right: 2% !important;
  }
}

.declaration {
  margin-top: 20px !important;
}

.radio-container-upload {
  padding: 24px 0px 10px 10px;
}
.radio-btn-upload {
  border: 1px solid #dcdcdc;
  padding: 0px 20px 0px 10px;
  border-radius: 20px;
  // font-size: 14px !important;
  &:focus-within {
    border: 1px solid #000;
  }
}

.text-muted-tags-upload {
  margin-top: -15px !important;
  margin-bottom: 20px !important;
  display: flex;
  justify-content: space-between;

  div {
    font-size: 12px;
    color: #6e6e6e;
    margin-left: 5px;
  }
}

.text-muted-upload-design {
  font-size: 12px;
  color: #6e6e6e;
  margin-left: 0px;
  position: relative;
  top: -2px;
}
.customizable {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}
.infopop {
  .MuiPopover-paper {
    background-color: #1b1918;
    opacity: 0.8 !important;
    width: 380px;
  }
}
.text-muted-tags-link-wrapper {
  margin-top: -15px !important;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  img {
    margin-right: 5px !important;
  }
}

.autocomplete-input {
  border-top-left-radius: 12px !important;
}

.upload-design-form {
  width: 50%;
  margin: 5px auto 5px auto !important;

  h2 {
    font-style: normal;
    margin-bottom: 0px !important;
    font-weight: 600;
    font-size: 14px;
    color: #1b1918;
  }
}

.grid-container {
  display: inline-grid !important;
  grid-template-columns: auto auto auto auto auto auto auto auto !important;
  padding: 10px !important;
}

.grid-item {
  border: 2px dashed #dcdcdc !important;
  border-radius: 8px !important;
  padding: 10px !important;
  margin: 10px !important;
  font-size: 30px !important;
  height: 70px !important;
  width: 70px !important;
}

.button-wrapper {
  display: flex !important;
  justify-content: space-between !important;
  min-width: 100% !important;
}

.upload-design-sub-titlee {
  text-align: center !important;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1b1918;
}

.upload-design-titlee {
  font-style: normal;
  font-weight: bolder !important;
  font-size: 24px;
  color: #000000;
  text-align: center;
  width: 100% !important;
}

.back-to-top {
  // margin-left: auto !important;
  position: fixed;
  bottom: 50px !important;
  right: 50px !important;
  .fabIcon {
    position: absolute;
    bottom: 0;
    right: 0px;
    border-radius: 50% !important;
    padding: 25px !important;
  }
}

@media (max-width: 600px) {
  .upload-design-form {
    width: 100%;
    margin: 5px 0 !important;
  }
}
